<template>
  <div id="single-blog">
     <h1>{{blog.title}}</h1>
     <article>{{blog.body}}</article>
     <!-- <article>{{blog.body}}</article>   等到有数据库后放开-->
  </div>
</template>

<script>
export default {
  name: 'single-blog',
  data(){
    return{
        id:this.$route.params.id,
        blog:{}
    }
  },
  created(){
      this.$http.get('http://jsonplaceholder.typicode.com/posts/'+this.id)
        .then(function(data){
          this.blog = data.body;
        })
        this.$http.get('http://jsonplaceholder.typicode.com/posts/'+this.id+".json")
        .then(function(data){
          return data;
        })
        .then(function(data){
          this.data = data;
        })
  }
}
</script>

<style scoped>
 #single-blog{
     max-width: 970px;
     margin:0 auto;
     padding:20px;
     border:1px dotted pink;
 }
</style>



