// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import VueResource from 'vue-resource'
import App from './App'
import VueRouter from 'vue-router'
import Routes from './routes'
Vue.config.productionTip = false
Vue.use(VueResource)
Vue.use(VueRouter)
//在main.js加的自定义指令、过滤器等都是全局的，可以被所有组件使用


//自定义指令Vue.directive('指令的名字(不包含v-)',{bind(el,binding,value)})
/*Vue.directive('rainbow',{
  bind(el,binding,vnode){
    //Math.random()产生0到1（包含0，不包含1）的随机数，toString(16)表示将随机数转换为16进制的字符串，slice(2,8)截取从第二个开始一直截取到第八个结束
    el.style.color = "#"+Math.random().toString(16).slice(2,8) 
  }
});
*/
Vue.directive('theme',{
  bind(el,binding,vnode){
    if(binding.value == 'wide'){
      el.style.maxwidth =  "1260px";
    }else if(binding.value == 'narrow'){
      el.style.maxwidth ="560px";
    }
    if(binding.arg == 'column'){
      el.style.background = "#861902" ;
      el.style.padding = "20px" ;
    }
  }
})
//自定义过滤器Vue.filter(id,[方法])；'|'指管道，'id'是管道后过滤器的名字
/*Vue.filter("upperCase",function(value){
  return value.toUpperCase();
})*/
Vue.filter("snippet",function(value){
  return value.slice(0,150) + '......'; //字符串截取0到100后拼接'.....'展示为省略的样式
})
 //创建路由
 const router = new VueRouter({
     routes:Routes,
     mode:"history"  //如果没有history时url会有#，因为此时mode为"hash"，既默认为mode:"hash"
 })
new Vue({
  el: '#app',
  components: { App },
  template: '<App/>',
  router:router
})



