<template>
  <div class="add-blog">
   <h2>添加博客</h2>
   <form v-if="!submmited" >
       <label for="">博客标题</label>
       <input type="text" v-model="blog.title" required /> 
        <label for="">博客内容</label>
       <textarea v-model="blog.content"> </textarea>
       <div id="checkboxes">
         <label for="">vue.js</label>
         <input type="checkbox" value="vue.js" v-model="blog.categories">
         <label for="">node.js</label>
         <input type="checkbox" value="nodee.js"  v-model="blog.categories">
         <label for="">react.js</label>
         <input type="checkbox" value="react.js"  v-model="blog.categories">
         <label for="">angular4.js</label>
         <input type="checkbox" value="angular4.js"  v-model="blog.categories">
       </div>
       <label>作者：</label>
       <select  v-model="blog.author">
         <option v-for="author in authors">{{author}}</option>
       </select>
       <button v-on:click.prevent="post">添加博客</button>
   </form>
   <div>
     <h3 v-if="submmited">您的博客发送成功</h3>
   </div>
   <div id="preview">
     <h3>博客总览</h3>
     <p>博客标题 {{blog.title}}</p>
     <p>博客内容：</p>
     <p>{{blog.content}}</p>
     <p>博客分类：</p>
     <ul>
       <li v-for="category in blog.categories">{{category}}</li>
     </ul>
     <p>作者：{{blog.author}}</p>
   </div>
  </div>
</template>

<script>
export default {
  //http://jsonplaceholder.typicode.com/    免费测试http请求的网站
  name: 'add-blog',
  data () {
    return {
       blog:{
           title:"",
           content:"",
           categories:[],
           author:""
       },
       authors:["zsj","ll","pyy"],
       submmited:false
    }
  },
  methods:{
    post:function(){
      this.$http.post("https://jsonplaceholder.typicode.com/posts",{
        title:this.blog.title,
        body:this.blog.content,
        userId:1
      })
        //this.$http.post("https://jsonplaceholder.typicode.com/posts",this.blog) 可写为这种方法
         .then(function(data){
           this.submmited = true;
         })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped> 
#add-blog *{
  box-sizing: border-box;
}
#add-blog{
  margin:20px auto;
  max-width: 600px;
  padding:20px;
}
label{
  display:block;
  margin:20px 0 10px;
}
input[type="text"],textarea,select{
  display: block;
  width: 100%;
  paffing:8px;
}
textarea{
  height:200px;
}
#checkboxes label{
  display: inline-block;
  margin-top:0;
}
#checkboxes input{
  display: inline-block;
  margin-right:10px;
}
button{
  display:block;
  margin: 20px 0;
  background: crimson;
  color: white;
  border: 0;
  padding:14px;
  border-radius: 4px;
  font-size: 18px;
  cursor: pointer;
}
#preview{
  padding: 10px 20px;
  border:1px dotted #ccc;
  margin:30px 0;
}
h3{
  margin-top: 10px;
}
</style>



