<template>
 <!-- <div id="show-blogs" v-theme="'wide'">-->  <!--v-theme="'wide'" 传字符串时必须用单引号引起来-->
   <div id="show-blogs" v-theme:column="'narrrow'">  <!--v-theme="'narrrow'"用来给自定义指令传参-->
      <h1>博客总览</h1>
      <input type="text" v-model="search" placeholder="搜索">
     <!-- <div v-for="blog in blogs" class="single-blog">-->
          <div v-for="blog in filterBlog" class="single-blog">
         <router-link v-bind:to="'/zsjBlog/' + blog.id">
         <h2 v-rainbow>{{blog.title | upperCase}}</h2>
         </router-link> 
          <article>
             {{blog.body | snippet}}
            <!-- {{blog.connet | snippet}}  数据库好使后放开-->
          </article>
      </div>
      
  </div>
</template>

<script>
export default {
  name: 'show-blogs',
  data(){
     return {
       blogs:[],
       search:""
     }
  },
  created(){
      //本地请求的文件只能放到static文件夹下
      this.$http.get('./../static/post.json')
        .then(function(data){
         this.blogs = data.body.slice(1,10); //获取十个数据对象      
      })
      /* 将数据库中的json对象换为数组  */
        this.$http.get('./../static/post.json')
        .then(function(data){
           //this.blogs = data.body.slice(1,10); //获取十个数据对象
           
           return data.json()
      })
      .then(function(data){
          var blogArray = [];
          for(let key in data){
          //  cosole.log(data[key]); //拿到以key为id的对象
            data[key].id = key; //将key赋给id
            blogArray.push(data[key]);
          }
          this.blog = blogArray;
      })
      
      

  },
  computed:{
      filterBlog:function(){
          //直接拿到blogs[]的每一条数据
        return this.blogs.filter((blog)=>{
              return blog.title.match(this.search); //如果匹配到则返回该条数据。否则返回所有数据
        })  
      }
  },
  //局部过滤器
  filters:{
     /*
     //方法一：
      "upperCase":function(value){
          return value.toUpperCase()
      },*/
      //方法二：
       upperCase(value){
          return value.toUpperCase()
      }
  },
  //局部自定义指令
  directives:{
          'rainbow':{
              bind(el,binding,vnode){
                el.style.color = "#"+Math.random().toString(16).slice(2,8)   
              }
          }
      }
}
</script>

<style scoped>
 #show-blogs{
     max-width:800px;
     margin:0 auto;
 }
 .single-blog{
     box-sizing: border-box;
     padding:20px;
     margin:20px 0;
     background:skyblue;
 }
 #show-blog a{
     color:#439823;
     text-decoration: none;
 }
 input[type="text"]{
     padding: 8px;
     width: 100%;
     box-sizing: border-box;
 }
</style>



