<template>
    <nav>
        <ul>
            <li>
                <router-link to="./add" exact> 写博客</router-link>  <!--exact控制单选，点击哪个另一个就取消选中效果-->
                <router-link to="./show" exact>博客浏览</router-link>
            </li>
        </ul>
    </nav>
</template>

<script>
export default {
    name:"BlogHeader"
}
</script>
<style scoped>
nav{
    background: #722007;
    padding: 30px 0;
    margin-bottom: 40px;
}
 ul{
   list-style-type: none;
   text-align: center;
   margin:0;
 }
 li{
   display: inline-block;
   margin: 0 10px;
 }
 a{
     color: aquamarine;
     text-decoration: none;
     padding: 12px;
     border-radius: 5px;
 }
 .router-link-active{
     background:rgba(255,255,100,0.7);
     color: #f1d6a7;
 }
</style>



